import { Divider } from "@material-ui/core"
import Header from "../Header"
import PropTypes from 'prop-types';
import { useAutocomplete } from '@mui/base/useAutocomplete';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';
import { autocompleteClasses } from '@mui/material/Autocomplete';
import { useForm } from "react-hooks-helper"
import InputAdornment from '@mui/material/InputAdornment'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import FormHelperText from '@mui/material/FormHelperText'
import axios from "axios"
import Select from '@mui/material/Select'
import { useEffect, useRef, useState } from "react";
import { TextField } from "@mui/material";


import Box from '@mui/material/Box';
import Fade from '@mui/material/Fade';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';

/*
    exercises: {
            value: [],
        }
         sessions_per_week: '',
        sessions_duration: '',
        IAP_periodicity: ''
        */
const Root = styled('div')(
    ({ theme }) => `
            color: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : 'rgba(0,0,0,.85)'
        };
            font-size: 14px;
          `,
);

const Label = styled('label')`
            padding: 0 0 4px;
            line-height: 1.5;
            display: block;
          `;

const InputWrapper = styled('div')(
    ({ theme }) => `
            width: 300px;
            border: 1px solid ${theme.palette.mode === 'dark' ? '#434343' : '#d9d9d9'};
            background-color: ${theme.palette.mode === 'dark' ? '#141414' : '#fff'};
            border-radius: 4px;
            padding: 1px;
            display: flex;
            flex-wrap: wrap;
          
            &:hover {
              border-color: ${theme.palette.mode === 'dark' ? '#177ddc' : '#40a9ff'};
            }
          
            &.focused {
              border-color: ${theme.palette.mode === 'dark' ? '#177ddc' : '#40a9ff'};
              box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
            }
          
            & input {
              background-color: ${theme.palette.mode === 'dark' ? '#141414' : '#fff'};
              color: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : 'rgba(0,0,0,.85)'
        };
              height: 30px;
              box-sizing: border-box;
              padding: 4px 6px;
              width: 0;
              min-width: 30px;
              flex-grow: 1;
              border: 0;
              margin: 0;
              outline: 0;
            }
          `,
);

function Tag(props) {
    const { label, onDelete, ...other } = props;
    return (
        <div {...other}>
            <span>{label}</span>
            <CloseIcon onClick={onDelete} />
        </div>
    );
}

Tag.propTypes = {
    label: PropTypes.string.isRequired,
    onDelete: PropTypes.func.isRequired,
};

const StyledTag = styled(Tag)(
    ({ theme }) => `
            display: flex;
            align-items: center;
            height: 24px;
            margin: 2px;
            line-height: 22px;
            background-color: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.08)' : '#fafafa'
        };
            border: 1px solid ${theme.palette.mode === 'dark' ? '#303030' : '#e8e8e8'};
            border-radius: 2px;
            box-sizing: content-box;
            padding: 0 4px 0 10px;
            outline: 0;
            overflow: hidden;
          
            &:focus {
              border-color: ${theme.palette.mode === 'dark' ? '#177ddc' : '#40a9ff'};
              background-color: ${theme.palette.mode === 'dark' ? '#003b57' : '#e6f7ff'};
            }
          
            & span {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          
            & svg {
              font-size: 12px;
              cursor: pointer;
              padding: 4px;
            }
          `,
);

const Listbox = styled('ul')(
    ({ theme }) => `
            width: 300px;
            margin: 2px 0 0;
            padding: 0;
            position: absolute;
            list-style: none;
            background-color: ${theme.palette.mode === 'dark' ? '#141414' : '#fff'};
            overflow: auto;
            max-height: 250px;
            border-radius: 4px;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
            z-index: 1;
          
            & li {
              padding: 5px 12px;
              display: flex;
          
              & span {
                flex-grow: 1;
              }
          
              & svg {
                color: transparent;
              }
            }
          
            & li[aria-selected='true'] {
              background-color: ${theme.palette.mode === 'dark' ? '#2b2b2b' : '#fafafa'};
              font-weight: 600;
          
              & svg {
                color: #1890ff;
              }
            }
          
            & li.${autocompleteClasses.focused} {
              background-color: ${theme.palette.mode === 'dark' ? '#003b57' : '#e6f7ff'};
              cursor: pointer;
          
              & svg {
                color: currentColor;
              }
            }
          `,
);

const Search = () => {
    const [skillsValues, setSkillsValues] = useState([{ title: '' }])
    const [academic_education, setAcademic_education] = useState('')
    const [formData, setForm] = useForm({
        // academic_education: '',
        academic_degree: '',
        time_experience: '',
        additional_information: ''
    })


    const [query, setQuery] = useState('idle');
    const timerRef = useRef();

    useEffect(
        () => () => {
            clearTimeout(timerRef.current);
        },
        [],
    )
    const handleClickQuery = () => {

        if (timerRef.current) {
            clearTimeout(timerRef.current);
        }

        if (query !== 'idle') {
            setQuery('idle');
            return;
        }

        setQuery('progress');
        var student = '202305042'
        const data = {
            studentID: student,
            searchID: "0009550"+student,
            academic_education: academic_education,
            academic_degree: formData.academic_degree,
            time_experience: parseInt(formData.time_experience),
            additional_information: formData.additional_information,
            skills: {
                value: value
            },
            data: new Date().toLocaleString()
        }

        console.log(data)

        axios.post('/api/searchActivity', data).then(result => {
            console.log(result)
            setTimeout(() => {
                if (result.data.status === "success") {
                    window.open(`${result.data.search_url.path}`, '_blank')
                    window.location.href = '/'
                }
            }, 2500)
        }).catch(e => {
            console.log(e);
        })

        timerRef.current = window.setTimeout(() => {
            setQuery('success');
        }, 2000);
    }

    /*
        skills: {
            value: []
        }
    */
    const {
        // academic_education,
        academic_degree,
        time_experience,
        additional_information
    } = formData


    const change = (e) => {
        console.log(e);
        setAcademic_education(e.target.value)
        console.log(academic_education);
        if (e.target.value === 'Engenharia Informatica') {
            setSkillsValues([
                { title: "Software analyst" },
                { title: "Software engineer" },
                { title: "Network engineer" },
                { title: "Front-end developer" },
                { title: "Fullstack developer" },
                { title: "Back-end developer" },
                { title: "Database management" },
                { title: "Data analyst" },
                { title: "AI specialist" },
                { title: "Database design" },
                { title: "UI Design" }
            ])
        }

        if (e.target.value === 'Engenharia de Minas') {
            setSkillsValues([
                { title: "Mine analyst" },
                { title: "Driling specialist" },
                { title: "Stone engineer" },
                { title: "Mine dilling analyst" }
            ])
        }

        if (e.target.value === 'Engenharia de Processamento Mineral') {
            setSkillsValues([
                { title: '' }
            ])
        }

    }


    const {
        getRootProps,
        getInputLabelProps,
        getInputProps,
        getTagProps,
        getListboxProps,
        getOptionProps,
        groupedOptions,
        value,
        focused,
        setAnchorEl,
    } = useAutocomplete({
        id: 'customized-hook-demo',
        defaultValue: [skillsValues[0]],
        multiple: true,
        options: skillsValues,
        isOptionEqualToValue: (option, value) => option.title === value.title,
        getOptionLabel: (option) => option.title,
    })

    useEffect(() => {
        console.log('select box---------')
    }, [])


    return (
        <div>
            <Header />
            <Divider style={{ marginBottom: '10px' }} />
            <h4>Buscar mentoria</h4>
            <hr></hr>

            <FormControl sx={{ minWidth: 250, marginTop: '10px', marginRight: '5px' }} size="small">
                <InputLabel id="demo-select-small-label">Curso</InputLabel>
                <Select
                    labelId="demo-select-small-label"
                    name="academic_education"
                    value={academic_education}
                    id="demo-select-small"
                    label="Formação"
                    onChange={change}
                //onSelect={change}
                >
                    <MenuItem value={'Engenharia Informatica'}>Engenharia Informatica</MenuItem>
                    <MenuItem value={'Engenharia de Minas'}>Engenharia de Minas</MenuItem>
                    <MenuItem value={'Engenharia de Processamento Mineral'}>Engenharia de Processamento Mineral</MenuItem>
                    <MenuItem value={'Any'}>Any formation--</MenuItem>
                </Select>
                <FormHelperText>Formação académica</FormHelperText>
            </FormControl>


            <FormControl sx={{ minWidth: 250, marginTop: '10px' }} size="small">
                <InputLabel id="demo-select-small-label">Nível académico</InputLabel>
                <Select
                    labelId="demo-select-small-label"
                    name="academic_degree"
                    value={academic_degree}
                    id="demo-select-small"
                    label="Nível académico"
                    onChange={setForm}
                >
                    <MenuItem value={'Licenciatura'}>Licenciatura</MenuItem>
                    <MenuItem value={'Mestrado'}>Mestrado</MenuItem>
                    <MenuItem value={'Doutoramento'}>Doutoramento</MenuItem>
                </Select>
                <FormHelperText>Grau/Nível de formação académica</FormHelperText>
            </FormControl>

            <Root style={{ marginTop: '10px', marginBottom: '10px' }}>
                <div {...getRootProps()}>
                    <Label {...getInputLabelProps()}><strong style={{ color: 'green' }}>Habilidades/competências do mentor</strong> <br></br>
                        <strong style={{ color: 'coral' }}><em>Seleccione no maximo três(3) Habilidades/competências</em></strong>
                    </Label>
                    <InputWrapper ref={setAnchorEl} className={focused ? 'focused' : ''}>
                        {value.map((option, index) => (
                            <StyledTag label={option.title} {...getTagProps({ index })} />
                        ))}
                        <input {...getInputProps()} />
                    </InputWrapper>
                </div>
                {groupedOptions.length > 0 ? (
                    <Listbox {...getListboxProps()}>
                        {groupedOptions.map((option, index) => (
                            <li {...getOptionProps({ option, index })}>
                                <span>{option.title}</span>
                                <CheckIcon fontSize="small" />
                            </li>
                        ))}
                    </Listbox>
                ) : null}
            </Root>

            <TextField
                label="#"
                name="time_experience"
                style={{ marginTop: '10px', marginRight: '10px', width: '120px' }}
                size="small"
                value={time_experience}
                onChange={setForm}
                helperText="Tempo de experiência"
                InputProps={{
                    endAdornment: <InputAdornment position="end">Anos</InputAdornment>,
                }}
            />

            <div class="input-group" style={{ marginTop: '10px' }}>
                <div class="input-group-prepend">
                    <span class="input-group-text">Informação adicional </span>
                </div>
                <textarea placeholder="Forneça-nos qualquer informação adicional sobre o seu background académico"
                    name="additional_information" value={additional_information} class="form-control" aria-label="With textarea"></textarea>
            </div>


            <br></br>
            <hr></hr>

            <Box sx={{ height: 40 }}>
                {query === 'success' ? (
                    <Typography style={{ color: 'green', borderBottom: '1px solid silver' }}>Pedido de busca criado com sucesso! <br></br> <strong style={{ fontSize: '12px' }}> <em>Nos iremos buscar para si o mentor adequado as especificações que nos foi fornecida. Obrigado!</em></strong></Typography>
                ) : (
                    <Fade
                        in={query === 'progress'}
                        style={{
                            transitionDelay: query === 'progress' ? '800ms' : '0ms', margin: '5px'
                        }}
                        unmountOnExit
                    >
                        <CircularProgress />
                    </Fade>
                )}
                <br></br>
                <button style={{ marginBottom: '50px' }} onClick={handleClickQuery} type="button" class="btn btn-info"> {query !== 'idle' ? 'Terminar' : 'BUSCAR'} </button>
            </Box>
            <i class="bi bi-arrows-angle-expand"></i>
        </div >
    )
}



export default Search