import React, { useEffect, useState } from 'react';
import { Card, Button, Modal, Container } from 'bootstrap-4-react'
import { useLocation } from 'react-router-dom';
import Header from '../Header';
import { Divider } from '@material-ui/core';
import axios from 'axios';
import WindowIcon from '@mui/icons-material/Window';
import ContentPasteSearchIcon from '@mui/icons-material/ContentPasteSearch';
import { styled } from '@mui/material/styles';
import Badge from '@mui/material/Badge';
import Avatar from '@mui/material/Avatar';

const ActivityProvider = ({ user }) => {
    const [iap, setIap] = useState([])
    const [IDS, setIDS] = useState([])
    const [request, setRequests] = useState([])
    const [mentors, setMentors] = useState([])
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const queryA = searchParams.get('searchID');
    const queryB = searchParams.get('studentID');
   
    useEffect(() => {

        if (queryA == null || queryB == null) {
            axios.get(`/api/getRequests?studentID=${user.studentID}`).then(result => {
                setIap(result.data.doc)
            })
        } else {
            axios.get(`/api/getRequests?searchID=${queryA}&studentID=${queryB}`).then(result => {
                setIap(result.data.doc)
            })
        }

        axios.get('/api/getMentors').then(result => {
            if (result.data.status === "success") {
                setMentors(result.data.doc)
            }
        })
        //Buscar plano

        
        
    }, [])



    useEffect(() => {
       // const k = await axios.get(`/api/getTemp?mentorID=${9550}`)
       axios.get(`/api/getRequest`).then(result => {
        setRequests(result.data.doc)
        console.log(result.data.doc);
       }) 

       //the array method may return if-condition value "mentorID exists" in the json-array
    }, [])
    

    return (
        <div>
            <Header />
            <React.Fragment>
                {iap.map(el => (<Card text="center">
                    <Card.Header>
                        <strong style={{ float: 'rigth' }}>Minhas solicitações</strong>
                    </Card.Header>
                    <Card.Body>
                        <Card style={{ width: '18rem' }}>
                            <Card.Body>
                                <Card.Title> <ContentPasteSearchIcon /> Search task</Card.Title>
                                <Card.Text>
                                    StudentID: <strong style={{ color: 'green' }}>{el.studentID}</strong> <br></br>
                                    SearchID: <strong>{el.searchID}</strong>
                                </Card.Text>
                            </Card.Body>
                            <Divider />
                            <Card.Body>
                                <div style={{ marginTop: '-10px' }}>
                                    <span style={{ fontSize: '13px' }}>Habilidades/Competências requeridas </span> {el.skills.value.map(e => (<span style={{ margin: '5px', color: 'white', borderRadius: '5px', backgroundColor: 'coral', padding: '3px', fontSize: '12px' }}>{e.title} <br></br></span>))}
                                </div>

                                <Divider style={{ marginTop: '10px', marginBottom: '10px' }} />
                                <span style={{ fontSize: '12px', color: 'green' }}><strong>Search status: </strong></span>
                                {el.activity_status === 'requested' ? <Button style={{ fontSize: '12px' }} warning><em>{el.activity_status}</em></Button> :
                                <Button style={{ fontSize: '12px' }} primary outline><em>{el.activity_status}</em></Button>}
                                <Divider style={{ marginTop: '10px', marginBottom: '10px' }} />
                                <Button info outline data-toggle="modal" data-target="#largeModal">< WindowIcon /> Resultados encontrados</Button>
                            </Card.Body>
                        </Card>
                    </Card.Body>





                    


                {/* Large modal */}
                <Modal id="largeModal" fade>
                    <Modal.Dialog lg>
                        <Modal.Content>
                            <Modal.Header>
                                <Modal.Title>Mentores encontrados (<span style={{color: 'green'}}>{mentors.length}</span>)</Modal.Title>
                                <Modal.Close>
                                    <span aria-hidden="true">&times;</span>
                                </Modal.Close>
                            </Modal.Header>
                            <Modal.Body>
                                <div class="card-group">
                                    {mentors.map(elem => (<div class="card">
                                        <div class="card-body">
                                            <p class="card-text">
                                                <StyledBadge
                                                    overlap="circular"
                                                    anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                                                    variant="dot"
                                                >
                                                    <Avatar alt="Remy Sharp" src={require('../../assets/image/' + elem.img)} />
                                                </StyledBadge>          
                
                                            </p>
                                            <strong>{elem.name}</strong>
                                            <p style={{float: 'right'}}>
                                                <span style={{fontSize: '12px'}}>Cargo: {elem.job_position}</span> | <span style={{fontSize: '12px'}}>Habilidades/Competências:</span>
                                            {elem.job_title.map(ele => (<span style={{ margin: '5px', color: 'white', borderRadius: '5px', backgroundColor: 'coral', padding: '3px', fontSize: '12px' }}>{ele} <br></br></span>))}
                                            <Divider style={{ marginTop: '5px', marginBottom: '5px' }} />
                                            <span style={{fontSize: '12px'}}>Nível académico: {elem.academic_degree}</span> <br></br>
                                            <span style={{fontSize: '12px'}}>Tempo de experiência: {elem.time_experience} (Anos)</span>
                                            <br></br>
                                            <Button success outline style={{marginTop:'10px', marginBottom: '-20px'}}
                                            onClick={() => {
                                                alert('Request')

                                                const body = {
                                                    searchObjectID: el._id,
                                                    requestID: el.studentID+""+el.searchID,
                                                    mentorID: elem.mentorID,
                                                    search: el,
                                                    student: user,
                                                    data: new Date().toLocaleString()
                                                }
                                                console.log(body);
                                                axios.post('/api/createRequest', body).then(result => {
                                                    console.log(result)
                                                })
                                            }}
                                            >Solicitar  mentoria</Button>
                                            </p>
                                        </div>
                                    </div>))}
                                </div>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button secondary data-dismiss="modal">Fechar</Button>
                                <Button primary onClick={() => {
                                    console.log(IDS);
                                    
                                }}>Salvar</Button>
                            </Modal.Footer>
                        </Modal.Content>
                    </Modal.Dialog>
                </Modal>
                </Card>))}


            </React.Fragment>
            {/*/updateSearchActivity*/}
        </div>
    )
}


const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
        backgroundColor: '#44b700',
        color: '#44b700',
        boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
        '&::after': {
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            borderRadius: '50%',
            animation: 'ripple 1.2s infinite ease-in-out',
            border: '1px solid currentColor',
            content: '""',
        },
    },
    '@keyframes ripple': {
        '0%': {
            transform: 'scale(.8)',
            opacity: 1,
        },
        '100%': {
            transform: 'scale(2.4)',
            opacity: 0,
        },
    },
}));

const SmallAvatar = styled(Avatar)(({ theme }) => ({
    width: 22,
    height: 22,
    border: `2px solid ${theme.palette.background.paper}`,
}));


export default ActivityProvider