import { Route, Routes } from "react-router-dom"
import './assets/css/App.css'
import { useEffect, useState } from "react"
import Home from "./pages/Home"
import Loger from "./pages/Loger"
import ErrorPage from "./pages/ErrorPage"
import Search from "./components/Invenira/Search"
import ActivityProvider from "./components/ActivityProvider/ActivityProvider"
import MentorArea from "./components/ActivityProvider/MentorArea"


const App = () => {
  const [user, setUser] = useState({})
  const [loading, setLoading] = useState(true)
  useEffect(() => {
    setUser({ usename: 'Student', studentID: '202305042', role: 'student' })
    setTimeout(() => {
      setLoading(false)
    }, 1000)
  },[])


  return (
    <div className="container">
      <Routes>
        <Route path='/' element={loading ? <Loger /> : <Home user={user}/>} />
        <Route path='/searchActivity/config' element={loading ? <Loger /> : <Search user={user} />}/>
        <Route path='/getSearchActivity' element={loading ? <Loger /> : <ActivityProvider user={user}/>} />
        <Route path='/page/activityProvider' element={loading ? <Loger /> : <MentorArea/>} />
        <Route path='*' element={<ErrorPage />} />
      </Routes>
    </div>
  )
}

export default App
