import { Divider } from "@material-ui/core"
import axios from "axios"
import { useEffect, useState } from "react"
import Header from "../components/Header";



const Home = ({user}) => {
    const [urlList, setUrls] = useState({})
    useEffect(() => {
        axios.get('/activityProvider').then(result => {
            console.log(result);
            setUrls(result.data)
        })
    }, [])

    return (
        <div>
            <Header />
            <Divider style={{ marginBottom: '10px' }} />
            <h4> Search Inventive Activity Plan</h4>
            <hr></hr>
            <div>

                <div class="card-group">
                    <div class="card">

                        <div class="card-body">
                            <h4 class="card-title">Buscar mentoria</h4>
                            <p class="card-text">
                                <button type="button" class="btn btn-info"
                                    style={{}}
                                    onClick={() => {
                                        var parser = document.createElement('a');
                                        parser.href = urlList.config_url
                                        window.open(parser.pathname)
                                    }}
                                >Open search</button>
                            </p>
                        </div>
                    </div>

                    <div class="card">
                        <div class="card-body">
                            <h4 class="card-title">Meus pedidos de mentoria</h4>
                            <p class="card-text">
                                <button type="button" class="btn btn-info"
                                    style={{}}
                                    onClick={() => {
                                        window.open('/getSearchActivity', '_blank')
                                    }}
                                >OPEN</button>
                            </p>
                        </div>
                    </div>

                    <div class="card">
                        <div class="card-body">
                            <h4 class="card-title">Sessões de mentoria</h4>
                            <p class="card-text">
                                <button type="button" class="btn btn-info"
                                    style={{}}
                                    onClick={() => {

                                    }}
                                >OPEN</button>
                            </p>
                        </div>
                    </div>
                </div>

                {/*
    var parser = document.createElement('a');
                            parser.href = regeditJSON.config_url
                            window.open(parser.pathname)
                            */}

            </div>
            <hr></hr>
            <p style={{ fontSize: '12px' }}>Developed <span style={{ color: 'dodgerblue' }}>@José Chemane</span></p>
        </div>
    )
}
export default Home