import React, { useEffect, useState } from 'react';
import { Card, Button, Modal, Container, Navbar, Nav, Form, Collapse } from 'bootstrap-4-react'
import { useLocation } from 'react-router-dom';
import Header from '../Header';
import { Divider } from '@material-ui/core';
import axios from 'axios';
import WindowIcon from '@mui/icons-material/Window';
import ContentPasteSearchIcon from '@mui/icons-material/ContentPasteSearch';
import { styled } from '@mui/material/styles';
import Badge from '@mui/material/Badge';
import Avatar from '@mui/material/Avatar';

const MentorArea = () => {
    const [iap, setIap] = useState([])
    const [IDS, setIDS] = useState([])
    const [request, setRequests] = useState([])
    const [mentors, setMentors] = useState([])

    useEffect(() => {
//getPedidos according mentorID user
        axios.get('/api/getPedidos').then(result => {
            console.log(result.data.doc)
            setRequests(result.data.doc)
        })
        //Buscar plano

    }, [])




    return (
        <div>
            <div>
                <Navbar expand="lg" light style={{ backgroundColor: '#e3f2fd' }} mb="3">
                    <Navbar.Brand href="#">MENTOR <strong style={{ fontSize: '13px' }}>Researcher</strong></Navbar.Brand>
                    <Navbar.Toggler target="#navbarColor3" />
                    <Collapse navbar id="navbarColor3">
                        <Navbar.Nav mr="auto">
                            <Nav.ItemLink href="#" active></Nav.ItemLink>
                        </Navbar.Nav>
                        <Form inline my="2 lg-0">
                            <Button outline primary my="2 sm-0">Mentor</Button>
                        </Form>
                    </Collapse>
                </Navbar>
            </div>
            <React.Fragment>
               {request.map(el => (<Card text="center">
                    <Card.Header>
                        <strong style={{ float: 'rigth' }}>Solicitações de mentoria</strong>
                    </Card.Header>
                    <Card.Body>
                        <Card style={{ width: '18rem' }}>
                            <Card.Body>
                                <Card.Title>Estudante</Card.Title>
                                <Card.Text>
                                    <strong>Dada da solicitação:</strong> <span style={{fontSize: '13px', color: 'green'}}>{el.data}</span> <br></br>
                                    <strong>Actor:</strong> <span style={{fontSize: '13px'}}>{el.student.usename}</span> <br></br>
                                    <strong>Código de estudante:</strong> <span style={{fontSize: '13px'}}>{el.student.studentID}</span> <br></br>
                                </Card.Text>
                            </Card.Body>
                            <Divider />
                            <Card.Body>
                                

                                <Button info outline data-toggle="modal" data-target="#largeModal">Aprovar</Button>
                            </Card.Body>
                        </Card>
                    </Card.Body>
                </Card>))}
            </React.Fragment>
            {/*/updateSearchActivity*/}
        </div>
    )
}


const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
        backgroundColor: '#44b700',
        color: '#44b700',
        boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
        '&::after': {
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            borderRadius: '50%',
            animation: 'ripple 1.2s infinite ease-in-out',
            border: '1px solid currentColor',
            content: '""',
        },
    },
    '@keyframes ripple': {
        '0%': {
            transform: 'scale(.8)',
            opacity: 1,
        },
        '100%': {
            transform: 'scale(2.4)',
            opacity: 0,
        },
    },
}));

const SmallAvatar = styled(Avatar)(({ theme }) => ({
    width: 22,
    height: 22,
    border: `2px solid ${theme.palette.background.paper}`,
}));


export default MentorArea